    overflow: auto;
    display: block;
    text-align: center;
    color: white;
    font-size: 15px;
    margin-top: 15px;
    border: 1px solid white;
    max-width: 100%;
    height: auto;
}

.sentinelsg4 img {
    float: left;
    width: 500px; 
    height: 300px;
    border-right: 15px solid rgba(0, 0, 0, 0);
    margin-right: 15px;
}

.sentinelsg4 h1 {
    text-align: center;
    letter-spacing: 3px;
    font-family: 'Montserrat';
}

.sentinelsg4 p {
    padding-left: 15px;
    padding-right: 15px;
    letter-spacing: 3px;
    font-family: 'Montserrat';
}

.s230 {
    overflow: auto;
    display: block;
    text-align: center;
    color: white;
    font-size: 15px;
    margin-top: 15px;
    border: 1px solid white;
    max-width: 100%;
    height: auto;
}

.s230 img {
    float: left;
    width: 500px; 
    height: 300px;
    border-right: 15px solid rgba(0, 0, 0, 0);
    margin-right: 15px;
}

.s230 h1 {
    text-align: center;
    letter-spacing: 3px;
    font-family: 'Montserrat';
}

.s230 p {
    padding-left: 15px;
    padding-right: 15px;
    letter-spacing: 3px;
    font-family: 'Montserrat';
}
.password form {
    display: block;
    text-align: center;
}

.undergroundpass {
    background: url(resources/background.png) no-repeat center fixed;
    background-size: cover;
    overflow: auto;
}

.homepass a{
    font-family: 'Montserrat';
    font-size: 30px;
    margin-inline: 1%;
}

.homepass a:hover {
    color: rgb(153, 153, 153);
}

.undergroundpass form {
    display: block;
    max-width: fit-content;
    max-height: fit-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 200px;
    background-color: black;
    border-radius: 10px;
}

input[type=password] {
    overflow: hidden;
    background-color: rgb(12, 12, 12);
    height: 75px;
    width: 300px;
    border:3px solid rgb(255, 255, 255);
    border-radius: 10px;
    color: rgb(255, 255, 255);
    text-decoration: none;
    font-size: 75px;
    text-align: center;
    letter-spacing: 40px;
    padding-left: 35px;
    font-family: 'Montserrat';
}

.underground {
    background-color: #1A1A1A;
    overflow: auto;
    zoom: 80%;
}
#background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    overflow: hidden;
}
.underground_topnav ul {
    display: flex;
    width: 100%;
}
.underground_topnav li {
    list-style-type: none;
}
.underground .back-btn {
    height: 36px;
    margin: 10px;
    z-index: 2;
}
.underground_topnav #bm-title {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    text-decoration: none;
    color: white;
    font-size: 45px;
    letter-spacing: 5px;
    color: #ffffff;
    font-family: 'Montserrat';
}

.underground form {   
    position: absolute;
    text-align: center;
    margin-block: 10%;
    margin-left: 20%;
    margin-right: 20%;
    font-size: 20px;
    color: #ffffff;
    background-color: #0000008a;
    border: 1px solid #ffffff;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 10px;
    font-family: 'Montserrat';
    letter-spacing: 3px;
}

input[type=text], select {
    width: 100%;
    padding: 15px 20px;
    margin: 8px 0;
    font-size: 130%;
    border: 1px solid rgb(110, 110, 110);
    border-radius: 4px;
    box-sizing: border-box;
    background-color: #181818;
    color: white;
    text-align: left;
    font-family: 'Montserrat';
    letter-spacing: 3px;
}

input[type=submit] {
    width: 100%;
    background-color: #ffffff1a;
    color: rgb(255, 255, 255);
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-family: 'Montserrat';
    font-size: 115%;
}
input[type=submit]:hover {
    background-color: #ffffff27;
}

.market-links {
    position: absolute;
    top: 10vh;
    left: 3vw;
    font-size: 30px;
    color: #ffffff;
    letter-spacing: 3px;
    width: 50%;
    font-family: 'Montserrat';
}

@keyframes imgfade {
    0% {
        filter: grayscale(0%);
    }
    100% {
        filter: grayscale(100%);
    }
}

.prices {
    text-align: center;
    font-size: 30px;
    color: white;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    font-family: 'Montserrat';
}
.prices h1, .prices p {
    padding: 10px;
}

.contactinfo {
    text-align: center;
    font-size: 20px;
    margin-top: 15px;
    color: white;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    font-family: 'Montserrat';
}
.contactinfo h1, .contactinfo h2, .contactinfo p {
    padding: 20px;
}
.contactinfo h1 {
    font-size: 50px;
}
.contactinfo h2 {
    font-size: 30px;
}
.contactinfo p {
    font-size: 25px;
}

a {
    text-decoration: none;
    color: white;
}

#underground-link {
    position: absolute;
    top: 0;
    left: 0;
    height: 10px;
    width: 10px;
}

.hero {
    display: flex;
    justify-content: center;
    border-bottom: 1px solid white;
}
.hero img {
    width: 100%;
    filter: brightness(50%);
}
.hero .text {
    position: absolute;
    color: white;
    top: 210px;
    font-size: 25px;
    text-align: center;
    font-family: 'Montserrat';
}
.hero .text button {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background-color: transparent;
    border: none;
    font-size: 30px;
    padding: 10px 30px;
    border-radius: 20px;
    background-color: rgba(51, 51, 51, 0.644);
    border: 1px solid white;
    margin-top: 15px;
}
.hero .text button:hover {
    background-color: rgba(51, 51, 51, 1);
}

#applicationForm {
    font-size: 20px;
    color: #ffffff;
    background-color: #0000008a;
    font-family: 'Montserrat';
    letter-spacing: 3px;
}
#applicationForm h1 {
    text-align: center;
    padding-block: 15px;
}
#applicationForm p {
    font-size: 25px;
    padding-inline: 10px;
}
#applicationForm input, select {
    padding-inline: 10px;
    border: none;
    border: 1px solid rgb(110, 110, 110)
}
#applicationForm input[type=submit] {
    font-size: 30px;
    border: none;
}
#applicationForm input[type=submit]:hover {
    background: rgba(51, 51, 51, 0.5);
    box-shadow: 0px 0px 20px 20px rgba(51, 51, 51, 0.5);
}
.events-container h1 {
    text-align: center;
    color: white;
    font-size: 5vw;
    padding: 15px;
    font-family: 'Montserrat';
}
.event-archive {
    text-align: center;
}
.event-archive h1 {
    color: white;
    padding: 15px;
    font-size: 4vw;
    font-family: 'Montserrat';
}
.event-archive img {
    width: 20%;
}
.leaderboard h1 {
    text-align: center;
    color: white;
    font-size: 5vw;
    padding: 30px;
}
.leaderboard ul {
    list-style: none;
    display: flex;
    justify-content: space-between;
    color: white;
    margin: 0 20%;
    border: 1px solid white;
}
.leaderboard li {
    text-align: center;
    font-size: 3.5vw;
    width: 20vw;
}
.leaderboard .score {
    text-align: right;
    border-left: 1px solid white;
}
.leaderboard .rank, .leaderboard .name {
    text-align: left;
}
.leaderboard .rank {
    border-right: 1px solid white;
}